/* Font import */
@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&display=swap");
/* General */
* {
  box-sizing: border-box;
  position: relative;
  cursor: default;
  margin: 0;
  padding: 0;
  overflow-x: hidden; }

html {
  width: 100vw;
  position: relative;
  margin: 0;
  padding: 0;
  font-size: 62.5%; }

body {
  width: 100vw;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  overflow-x: hidden;
  background-attachment: fixed;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center; }

button {
  font-family: "Josefin Sans";
  border: none;
  background: transparent;
  cursor: pointer; }

/* Components */
.app {
  width: 100vw;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: "Josefin Sans";
  font-size: 1.8rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center; }

.image-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: 20rem;
  background-position: center;
  background-size: cover; }

.light {
  background-image: url("../assets/images/bg-mobile-light.jpg"); }

.dark {
  background-image: url("../assets/images/bg-mobile-dark.jpg"); }

/* Media Query */
@media only screen and (min-width: 600px) {
  .image-background {
    height: 30rem; }

  .light {
    background-image: url("../assets/images/bg-desktop-light.jpg"); }

  .dark {
    background-image: url("../assets/images/bg-desktop-dark.jpg"); } }
.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 32.7rem;
  height: 100%; }

@media only screen and (min-width: 600px) {
  .wrapper {
    width: 54rem;
    height: 100%; } }
.header {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  margin: 4.8rem 0 4rem 0; }
  .header .app-title {
    text-transform: uppercase;
    color: white;
    font-weight: 700;
    letter-spacing: 0.36em;
    font-size: 2.8rem; }
  .header button {
    width: 2.4rem;
    height: 2.4rem;
    background-position: center;
    background-size: cover; }
  .header .toggle-light {
    background-image: url("../assets/images/icon-moon.svg"); }
  .header .toggle-dark {
    background-image: url("../assets/images/icon-sun.svg"); }

/* Media Queries */
@media only screen and (min-width: 600px) {
  .header {
    margin: 7.7rem 0 5rem 0; }
    .header .app-title {
      letter-spacing: 0.415em;
      font-size: 4rem; }
    .header button {
      width: 2.7rem;
      height: 2.7rem; } }
.input-form {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100%;
  border-radius: 0.6rem;
  min-height: 4.8rem;
  padding-left: 2rem; }
  .input-form input {
    font-family: "Josefin Sans";
    border: none;
    font-size: 1.3rem;
    flex-grow: 1;
    min-height: 4.8rem; }
    .input-form input:focus {
      outline: none; }

/* Media queries */
@media only screen and (min-width: 600px) {
  .input-form {
    min-height: 6.4rem; }
    .input-form input {
      min-height: 6.4rem;
      font-size: 1.9rem; } }
.todo-item {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  min-height: 5rem;
  padding: 0 2rem 0 2rem;
  border-bottom: 0.1rem solid; }
  .todo-item .todo-content {
    max-width: 24.1rem;
    text-align: start;
    font-size: 1.3rem;
    font-weight: regular;
    overflow-wrap: break-word;
    cursor: pointer; }
    .todo-item .todo-content:after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 50%; }
  .todo-item .remove-todo-btn {
    position: absolute;
    top: 50%;
    left: 100;
    right: 0;
    bottom: 0;
    transform: translateY(-50%);
    margin-right: 2rem;
    width: 1.4rem;
    height: 1.4rem;
    background-image: url("../assets/images/icon-cross.svg");
    background-position: center;
    background-size: cover; }

.round-btn {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 2rem;
  height: 2rem;
  border: 0.1rem solid;
  border-radius: 50%;
  margin-right: 1.2rem; }

.completed {
  background: linear-gradient(135deg, #57ddff, #c058f3);
  border: none; }
  .completed .icon-check {
    width: 1rem;
    height: 1rem;
    background-image: url("../assets/images/icon-check.svg");
    background-position: center;
    background-repeat: no-repeat; }

/* Media Queries */
@media only screen and (min-width: 600px) {
  .todo-item {
    min-height: 6.4rem;
    padding: 0 2.4rem 0 2.4rem; }
    .todo-item .todo-content {
      font-size: 1.9rem;
      max-width: 41rem; }
    .todo-item .remove-todo-btn {
      width: 1.8rem;
      height: 1.8rem;
      margin-right: 2.4rem;
      display: none; }
    .todo-item:hover .remove-todo-btn {
      display: block; }
    .todo-item:hover .round-btn {
      background: linear-gradient(135deg, #57ddff, #c058f3);
      border: none; }
      .todo-item:hover .round-btn:after {
        display: block; }
    .todo-item:hover .completed:after {
      display: none; }

  .round-btn {
    width: 2.6rem;
    height: 2.6rem;
    margin-right: 2.4rem; }
    .round-btn:after {
      display: none;
      content: "";
      width: 2.4rem;
      height: 2.4rem;
      border-radius: 50%; } }
.todo-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  margin-top: 1.6rem;
  border-radius: 0.6rem;
  min-height: 36.8rem; }

.todo-list {
  width: 100%; }

