.filter-panel {
  width: 100%;
  height: 4.8rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-radius: 0.6rem;
  margin-top: 1.6rem;
  padding: 0 8rem 0 8rem; }
  .filter-panel button {
