@media only screen and (min-width: 600px) {
  .filter-panel {
    width: 16.7rem;
    height: 5rem;
    position: relative;
    top: -5rem;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 0;
    margin: 0;
    background: transparent; }
    .filter-panel button {
      font-size: 1.6rem; } }
.sub-text {
  padding-top: 4.2rem;
  font-size: 1.5rem; }

@media only screen and (min-width: 600px) {
  .sub-text {
    padding-top: 0.3rem; } }
